.app {}

.Header {
    border-bottom: 1px solid blue;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Grid {
    display: grid;
    grid-template-columns: 2;
    column-gap: 1px;
    row-gap: 1px;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}